import { StringOrNull } from '../Base';

import { MAX_REPORT_TYPE_CODE_LENGTH, NonEmptyString } from './BaseString';

export class ReportTypeCode extends NonEmptyString<
  'ReportTypeCode',
  StringOrNull
> {
  static from(value: StringOrNull): ReportTypeCode {
    return new ReportTypeCode(value);
  }
  protected validate(value: StringOrNull): StringOrNull {
    value = super.validate(value);

    if (value && value.length > MAX_REPORT_TYPE_CODE_LENGTH) {
      throw new Error(
        `ReportTypeCode has maximum length of ${MAX_REPORT_TYPE_CODE_LENGTH}`,
      );
    }

    return value;
  }
}
